.header {
  display: flex;
  max-width: 1440px;
  background-color: rgba(196, 196, 196, 0.01);
  margin-left: auto;
  margin-right: auto;
  justify-content: space-between;
  position: relative;
  z-index: 4;
  outline: 1px solid rgba(255, 255, 255, 0.2);
}

.header_saved {
  box-shadow: inset 0px -1px 0px #d1d2d6;
}

.header_mobile::before {
  content: "";
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
  background-color: rgba(0, 0, 0, .5);
}

.header__logo {
  width: 134px;
  font-family: 'Roboto Slab', 'Arial', sans-serif;
  font-weight: 900;
  font-size: 20px;
  line-height: 24px;
  margin-top: 30px;
  margin-left: 104px;
  margin-bottom: 26px;
  text-decoration: none;
}

.header__logo_dark {
  color: #000;
}

.header__logo_white {
  color: #fff;
}

.header__logo:hover {
  opacity: 0.7;
}

.header__button-mobile {
  display: none;
  width: 24px;
  height: 24px;
  background: none;
  border: none;
  background-image: url(../../images/menu-burger.png);
  cursor: pointer;
  margin-top: 16px;
  margin-right: 16px;
  outline: none;
}

.header__button-mobile_close {
  background-image: url(../../images/back.png);
}

.header__button-mobile_close_white {
  background-image: url(../../images/close.png);
}

.header__button-mobile_black {
  background-image: url(../../images/burger-menu-black.png);
}

.header__button-mobile:hover {
  opacity: 0.7;
}

@media screen and (max-width: 1100px) {
  .header__logo {
    margin-left: 70px;
  }
}

@media screen and (max-width: 900px) {
  .header__logo {
    margin-top: 22px;
    margin-left: 40px;
    margin-bottom: 18px;
  }
}

@media screen and (max-width: 700px) {

  .header__button-mobile {
    display: block;
  }

  .header__logo {
    margin-top: 16px;
    margin-left: 16px;
    margin-bottom: 16px;
  }

  .header_mobile_sticking {
    background: #fff;
    position: -webkit-sticky; /* Safari */
    position: sticky;
    top: 0;
  }
}


@media screen and (max-width: 330px) {

  .header__button-mobile_hidden {
    display: none;
  }
}
