.preloader {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  display: none;
  flex-direction: column;
  justify-content: center;
}

.preloader_active {
  display: flex;
}

.preloader__circle {
	display: block;
	width: 74px;
	height: 74px;
	border: 3px solid #444;
	border-bottom-color: #888;
	border-radius: 50%;
	margin: 80px auto 0 auto;
	animation: spin .75s infinite linear;
}

@keyframes spin {
	100% {
		transform: rotate(360deg);
	}
}

.preloader__title {
  max-width: 280px;
  font-family: 'Roboto', 'Arial', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: #b6bcbf;
  margin: 24px auto 80px auto;
}